*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  font-family: "Source Sans Pro", "Trebuchet MS", "Lucida Grande",
    "Bitstream Vera Sans", "Helvetica Neue", sans-serif;
  color: #999;
}

header {
  margin: 1em auto 2em;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.3em;
  font-size: 2.5rem;
}

header > img {
  max-width: 60px;
}

header + div > div:first-child {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5em;
}

button {
  width: 1.25em;
  text-align: center;
  font-size: 3rem;
  background-color: #999;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button + div {
  min-width: 2em;
  text-align: center;
  font-size: 3rem;
}
